import React from "react"

function Topbar() {
    return (
        <div id="topbar" className="d-none d-lg-flex align-items-center fixed-top">
        <div className="container d-flex">
          <div className="contact-info mr-auto">
            <i className="icofont-envelope" /> <a href="mailto:contact@example.com">inquiries@tagpay.co</a>
            <i className="icofont-phone" /> +44 (0) 20 3900 1254
          </div>
          <div className="social-links">
            <a href="https://twitter.com/tagpay_" className="twitter"><i className="icofont-twitter" /></a>
            <a href="https://www.facebook.com/tagpay.co/" className="facebook"><i className="icofont-facebook" /></a>
            <a href="https://www.instagram.com/tagpay_/" className="instagram"><i className="icofont-instagram" /></a>
            <a href="https://twitter.com/" className="skype"><i className="icofont-skype" /></a>
            <a href="https://twitter.com/" className="linkedin"><i className="icofont-linkedin" /></a>
          </div>
        </div>
      </div>
    )
}

export default Topbar