import React from "react"

function OrderNow() {
    return (
        <section id="OrderNow" className="OrderNow">
            <div className="row">
                <button type="button" class="btn btn-primary btn-lg">Order Now</button>
            </div>
            <div className="row">
                <h5>Pre orders accepted from December 15, 2020</h5>
            </div>
            <div className="row">
                <h6>Contact one of our authorised territorial represenatives for questions.</h6>
            </div>
        </section>
    )
}

export default OrderNow