import React from "react"

function PriceBox (props) {

    return (
            <div className="col-md-6 col-lg-3 d-flex align-items-stretch mb-5 mb-lg-0">
                <div className="icon-box" style={{backgroundColor : props.color,}} data-aos="fade-up" data-aos-delay={100}>
                    <h2 className="title">{props.title}</h2>
                    <h6 className="price">{props.price} mo</h6>
                    <h4 className="desc">£{props.price} per month plus 2.9% transactional fees ( subject to geo )</h4>
                    <p className="description">A {props.title} solution for players,parents, and coaches while enrolled in sport - activities academiesand or camps.</p>
                    <p className="participants">{props.participants}</p>   
                    <p className="description">A {props.title} solution for players,parents, and coaches while enrolled in sport - activities academiesand or camps.</p>             
                </div>
            </div>
    )
}
export default PriceBox