import React from "react"
import PriceBox from "./PriceBox"
import OrderNow from "./OrderNow"

function Pricing () {
    return (
        <section id="featured-services" className="featured-services">
            <div className="container" data-aos="fade-up">
                <div className="row">
                    <PriceBox 
                        title={"Lite"} 
                        price={"9.99"} 
                        participants={"Up to 30 participants."}
                        color={"#2A99CC"}
                    />
                    <PriceBox 
                        title={"Standard"} 
                        price={"18.00"} 
                        participants={"Up to 90 participants."}
                        color={"#005EAB"}
                    />
                    <PriceBox 
                        title={"Pro"} 
                        price={"--"} 
                        participants={"Unlimited participants*."}
                        color={"#000000"}
                    />
                </div>
            </div>
            <OrderNow />
        </section>
    )
}

export default Pricing